*{margin:0;padding:0}html{scroll-behavior:smooth;font-family:"Poppins",sans-serif;color:#2a2a2a;font-size:calc(12px + (20 - 12) * ((100vw - 300px) / (1600 - 300)));overflow-x:hidden}body{background:linear-gradient(-90deg, rgba(255, 255, 255, 0.05) 1px, transparent 1px),linear-gradient(rgba(255, 255, 255, 0.05) 1px, transparent 1px),linear-gradient(-90deg, rgba(255, 255, 255, 0.04) 1px, transparent 1px),linear-gradient(rgba(255, 255, 255, 0.04) 1px, transparent 1px),linear-gradient(transparent 3px, white 3px, white 78px, transparent 78px),linear-gradient(-90deg, white 1px, transparent 1px),linear-gradient(-90deg, transparent 3px, white 3px, white 78px, transparent 78px),linear-gradient(white 1px, transparent 1px),#fff;background-size:4px 4px,4px 4px,80px 80px,80px 80px,80px 80px,80px 80px,80px 80px,80px 80px}::selection{background:#db7546}::-moz-selection{background:#db7546}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:rgba(0,0,0,.5)}::-webkit-scrollbar-thumb{border-radius:0px;background:#2a2a2a}
.App{padding:20px 20px}.App .title_text{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}.App a{color:#2a2a2a;transition:color .2s;text-decoration:none}.App a:hover{color:#db7546}@media only screen and (min-width: 1000px){.App{padding:20px 150px}}
.cursor{width:40px;height:40px;border:2px solid #db7546;border-radius:100%;position:fixed;transform:translate(-50%, -50%);pointer-events:none;transition:all 150ms ease;transition-property:background-color,opacity,transform,mix-blend-mode;z-index:9999;mix-blend-mode:difference}.cursor--hidden{opacity:0}.cursor--link-hovered{transform:translate(-50%, -50%) scale(1.25);background-color:#db7546}.cursor--clicked{transform:translate(-50%, -50%) scale(0.9);background-color:#db7546}html *,body *{cursor:none}
nav{z-index:999;position:fixed;top:0px;left:0;right:0;padding:10px 0;display:flex;justify-content:center;grid-gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));background-color:rgba(255,255,255,.95);box-shadow:0 1px 1px 0 rgba(0,0,0,.05),0 1px 10px 0 rgba(0,0,0,.5)}
footer{position:relative;min-height:100vh}footer .footer__bg{background:#2a2a2a no-repeat center center fixed;background-size:cover;position:absolute;top:0;bottom:-20px;left:-20px;right:-20px;z-index:-1}footer .contact__description{display:flex;flex-direction:column;align-items:flex-end}footer .contact__description p{color:#fff;text-align:right;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));font-weight:600;max-width:calc(200px + (500 - 200) * ((100vw - 300px) / (1600 - 300)));padding:75px 0}footer .contact__description a{color:#db7546}footer .copyright{color:#fff;display:flex;position:absolute;bottom:0px;left:0;right:0;justify-content:center}@media only screen and (min-width: 1000px){footer .footer__bg{left:-200px;right:-200px}}
.about{display:flex;flex-direction:column;height:calc(100vh - 20px)}.about p{margin-top:10px}.about .about__intro{text-align:right;flex-direction:column;margin-top:100px}.about .about__intro .about__intro__title h1:last-child{margin-top:-10px}.about .about__intro .about__intro__subtitle{font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)))}.about .about__description{margin-top:50px;transition:.5s max-height cubic-bezier(0.19, 1, 0.22, 1);overflow:hidden}.about .about__description__show{max-height:300px}.about .about__description__show a{text-decoration:underline}.about .about__description__hide{max-height:0px}.about .about__more{text-align:right;position:relative;margin-top:15px}
.work{margin:0 0 100px}.work h1{padding-top:50px}.work .work__category{margin:50px 0;display:flex;justify-content:center;grid-gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}.work .work__category p{padding:15px;box-shadow:0 1px 1px 0 rgba(0,0,0,.05),0 1px 10px 0 rgba(0,0,0,.5)}.work .work__category .activeOption{color:#000 !important;background-color:#db7546}.work .work__grid{-webkit-column-count:2;column-count:2;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}.work .work__grid .work__item{box-shadow:0 1px 1px 0 rgba(0,0,0,.05),0 1px 10px 0 rgba(0,0,0,.5);display:grid;grid-template-rows:1fr auto;margin-bottom:10px;page-break-inside:avoid;-webkit-column-break-inside:avoid;break-inside:avoid}.work .work__grid .work__item img{width:100%;display:block;grid-row:1/-1;grid-column:1}.work .work__grid .work__item p{background-color:#fff;padding:.2em .5em}.work .work__grid .work__item:hover p{background-color:#db7546;color:#fff}@media only screen and (min-width: 700px){.work .work__grid{-webkit-column-count:3;column-count:3}}@media only screen and (min-width: 1000px){.work{margin-top:100px}}
.work__page{margin:75px 0}.work__page h1{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}.work__page .work__page__content{margin:50px 0;display:grid;grid-template-columns:2fr 3fr;grid-gap:20px;gap:20px}.work__page .work__page__content .temp_images{width:100%;background-color:#000}@media only screen and (max-width: 750px){.work__page .work__page__content{grid-template-columns:1fr}}
.resume{margin-top:75px}.resume h1{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}.resume h2{font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)))}.resume .resume__section{padding:20px 0;border-bottom:solid #2a2a2a 1px}.resume .resume__section__grid{display:grid;grid-template-columns:1fr}@media only screen and (min-width: 650px){.resume .resume .resume__section__grid{grid-template-columns:1fr 1fr}}.resume .resume__item{box-shadow:0 1px 1px 0 rgba(0,0,0,.01),0 6px 20px 0 rgba(0,0,0,.19);margin:10px;padding:10px;position:relative}.resume .resume__item .resume__item__inner{margin:0 25px}.resume .resume__item .resume__item__description__toggle{border-top:solid 1px #2a2a2a;margin-top:15px}.resume .resume__item .resume__item__description{transition:.5s max-height;overflow:hidden;padding:0 25px}.resume .resume__item .resume__item__description li{list-style:circle}.resume .resume__item .resume__item__description__show{max-height:300px}.resume .resume__item .resume__item__description__hidden{max-height:0}
.blog{margin-top:75px}.blog h1{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}.blog .coming_soon{min-height:100vh;background-color:wheat;overflow:hidden}.blog .coming_soon .coming_soon_0{background-color:#fff;padding:50px}.blog .coming_soon .coming_soon_1{background-color:violet;padding:50px}
